import React, { Component } from 'react';

import 'antd/dist/antd.css';
import { Button, Checkbox, DatePicker, Form, Input, InputNumber, Radio, Row, Select, Typography, Col } from 'antd';
import moment from 'moment';
import { getDistrictsByProvinceCode, getProvinces, getWardsByDistrictCode } from 'sub-vn';

const { Text } = Typography;
const date = new Date();

const day = [ 'Th hai', 'Th ba', 'Th t', 'Th nm', 'Th su', 'Th by', 'Ch nht' ];
const timeDay = [
	{ label: 'Sng', value: 'Th hai - sng' },
	{ label: 'Sng', value: 'Th ba - sng' },
	{ label: 'Sng', value: 'Th t - sng' },
	{ label: 'Sng', value: 'Th nm - sng' },
	{ label: 'Sng', value: 'Th su - sng' },
	{ label: 'Sng', value: 'Th by - sng' },
	{ label: 'Sng', value: 'Ch nht - sng' },
	{ label: 'Chiu', value: 'Th hai - chiu' },
	{ label: 'Chiu', value: 'Th ba - chiu' },
	{ label: 'Chiu', value: 'Th t - chiu' },
	{ label: 'Chiu', value: 'Th nm - chiu' },
	{ label: 'Chiu', value: 'Th su - chiu' },
	{ label: 'Chiu', value: 'Th by - chiu' },
	{ label: 'Chiu', value: 'Ch nht - chiu' },
	{ label: 'Ti', value: 'Th hai - ti' },
	{ label: 'Ti', value: 'Th ba - ti' },
	{ label: 'Ti', value: 'Th t - ti' },
	{ label: 'Ti', value: 'Th nm - ti' },
	{ label: 'Ti', value: 'Th su - ti' },
	{ label: 'Ti', value: 'Th by - ti' },
	{ label: 'Ti', value: 'Ch nht - ti' }
];

class HireOrderForm extends Component {
	constructor() {
		super();
		this.numberOfLesson = 1;
		this.provinceCode = '';
		this.provinceName = '';
		this.districtCode = '';
		this.districtName = '';
		this.wardCode = '';
		this.wardName = '';
		this.err = '';
	}

	state = {
		loading: false,
		visible: false,
		dateContract: '',
		totalMoneyContract: 1,
		hourPerLesson: 1.5,
		selectedItems: [],
		checkedList: [],
		indeterminate: true,
		provinceCode: this.provinceCode,
		districtCode: this.districtCode,
		wardCode: this.wardCode
	};

	componentWillReceiveProps(nextProp) {
		this.setState({
			visible: nextProp.visible
		});
	}

	handleSubmit = (e) => {
		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			if (!err) {
				console.log('Received values of form: ', values);
			}
		});
	};

	onChangeRadio = (e) => {
		const st = this.props;
		this.setState({
			hourPerLesson: e.target.value,
			totalMoneyContract: st.moneyTeacherPerHour * parseInt(this.numberOfLesson) * e.target.value
		});
		this.hourPerLesson = e.target.value;
	};

	handleChange = (selectedItems) => {
		this.setState({ selectedItems });
	};

	onChangeTimeDay = (checkedList) => {
		this.setState({
			checkedList,
			indeterminate: !!checkedList.length && checkedList.length < timeDay.length
		});
	};

	render() {
		const st = this.props;
		const { selectedItems } = this.state;
		const OPTIONS = st.skills;
		const filteredOptions = OPTIONS.filter((o) => !selectedItems.includes(o));
		const { getFieldDecorator } = this.props.form;
		return (
			<Form layout="vertical" onSubmit={this.handleSubmit} style={{ maxHeight: '100%' }}>
				<Row gutter={15}>
					<Col span={12}>
						<Form.Item label="Tn gio vin">
							<Input style={{ width: '100%' }} value={st.nameTeacher} readOnly />
						</Form.Item>
					</Col>
					<Col span={12}>
						<Form.Item label="Tn hc sinh">
							<Input style={{ width: '100%' }} value={st.nameStudent} readOnly />
						</Form.Item>
					</Col>
				</Row>
				<Form.Item label="Gii tnh">
					<Input style={{ width: '100%' }} value={st.genderStudent} readOnly />
				</Form.Item>
				<Row gutter={15}>
					<Col span={12} />
				</Row>
				<Form.Item label={<span>Ph dy 1 gi (VND)&nbsp;</span>}>
					<InputNumber
						style={{ width: '100%' }}
						value={st.moneyTeacherPerHour}
						readOnly
						min={50000}
						step={10000}
						size="large"
						formatter={(value) => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
						parser={(value) => value.replace(/\$\s?|(,*)/g, '')}
					/>
				</Form.Item>
				<Form.Item label="Mn hc">
					{getFieldDecorator('subject', {
						rules: [
							{
								required: true,
								message: 'Vui lng chn mn hc'
							}
						]
					})(
						<Select
							mode="multiple"
							placeholder="Chn mn hc"
							defaultValue={st.skills}
							onChange={this.handleChange}
							style={{ width: '100%', textAlign: 'center' }}
						>
							{filteredOptions.map((item) => (
								<Select.Option key={item} value={item}>
									{item}
								</Select.Option>
							))}
						</Select>
					)}
				</Form.Item>
				<Form.Item label="S lng bui hc">
					{getFieldDecorator('fee', {
						rules: [
							{
								required: true,
								message: 'Vui lng in s lng bui hc'
							}
						]
					})(
						<Input
							style={{ width: '100%' }}
							onChange={(event) => {
								this.numberOfLesson = event.target.value.toString();
							}}
							min={1}
							defaultValue={2}
							placeholder="S lng bui hc"
						/>
					)}
				</Form.Item>
				<Form.Item label="S gi mi bui">
					{getFieldDecorator('hour', {
						rules: [
							{
								required: true,
								message: 'Vui lng chn s gi mi bui'
							}
						]
					})(
						<Radio.Group onChange={this.onChangeRadio} value={this.state.hourPerLesson}>
							<Radio value={1.5}>1.5 gi</Radio>
							<Radio value={2}>2 gi</Radio>
							<Radio value={2.5}>2.5 gi</Radio>
							<Radio value={3}>3 gi</Radio>
						</Radio.Group>
					)}
				</Form.Item>
				<Row gutter={15}>
					<Col span={12}>
						<Form.Item label="Chn tnh/ Thnh ph">
							<Select
								labelInValue
								onChange={(value) => {
									this.setState({ provinceCode: value.key, provinceName: value.label });
								}}
								showSearch
								optionFilterProp="children"
								filterOption={(input, option) =>
									option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
								size="large"
							>
								{getProvinces().map((con) => (
									<Select.Option value={con.code}>{con.name}</Select.Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col span={6}>
						<Form.Item label="Chn huyn/ th x">
							<Select
								showSearch
								labelInValue
								onChange={(value) => {
									this.setState({ districtCode: value.key, districtName: value.label });
								}}
								optionFilterProp="children"
								filterOption={(input, option) =>
									option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
								size="large"
							>
								{getDistrictsByProvinceCode(this.state.provinceCode).map((con) => (
									<Select.Option value={con.code}>{con.name}</Select.Option>
								))}
							</Select>
						</Form.Item>
					</Col>
					<Col span={6}>
						<Form.Item label="Chn phng">
							<Select
								labelInValue
								onChange={(value) => {
									this.setState({ wardCode: value.key, wardName: value.label });
								}}
								showSearch
								optionFilterProp="children"
								filterOption={(input, option) =>
									option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
								size="large"
							>
								{getWardsByDistrictCode(this.state.districtCode).map((con) => (
									<Select.Option value={con.code}>{con.name}</Select.Option>
								))}
							</Select>
						</Form.Item>
					</Col>
				</Row>

				<Form.Item label="a ch c th din ra hc">
					<Input
						size="large"
						onChange={(event) => {
							this.address = event.target.value;
						}}
						name="address"
					/>
				</Form.Item>

				<Form.Item label="Lch hc trong tun">
					{getFieldDecorator('schedule', {
						rules: [
							{
								required: true,
								message: 'Vui lng chn lch hc ca bn!'
							}
						]
					})(
						<div>
							<div
								style={{
									width: '100%',
									display: 'flex',
									flexDirection: 'row'
								}}
							>
								<div
									style={{
										width: '100%',
										display: 'flex',
										flexDirection: 'row'
									}}
								>
									{day.map((day) => (
										<div style={{ width: '14%' }}>
											<strong>{day}</strong>
										</div>
									))}
								</div>
							</div>
							<Row>
								<Checkbox.Group options={timeDay} onChange={this.onChangeTimeDay} />
							</Row>
						</div>
					)}
				</Form.Item>
				<Form.Item label="Ngy yu cu hp ng dy">
					<DatePicker
						style={{ width: '100%' }}
						value={moment(date, 'DD/MM/YYYY')}
						format="DD/MM/YYYY"
						onChange={(value) => {
							this.setState({ dateContract: value.format('YYYY-MM-DD') });
						}}
					/>
				</Form.Item>
				<Form.Item label="Ghi ch thm">
					<Input.TextArea
						rows={2}
						placeholder="Ghi ch nhng thng tin cn thit khc"
						onChange={(event) => {
							this.note = event.target.value;
						}}
					/>
				</Form.Item>
				<Form.Item>
					<Checkbox checked={true}>Bn ng  vi nhng iu khon ca Bmentor</Checkbox>
				</Form.Item>
				<div>
					<strong>{this.err}</strong>
				</div>
				{st.curMoneyStudent < this.state.totalMoneyContract && (
					<div style={{ justifyContent: 'center' }}>
						<strong>
							S tin hin c ({st.curMoneyStudent}) khng   thu gia s ny. Bn phi c ti thiu s
							tin l ({this.state.totalMoneyContract})
						</strong>
					</div>
				)}
				<Form.Item
					style={{
						width: '100%',
						display: 'flex',
						flexDirection: 'row',
						justifyContent: 'flex-end'
					}}
				>
					<Button onClick={this.handleCancel} style={{ marginRight: 20 }}>
						Hy
					</Button>
					{st.curMoneyStudent >= this.state.totalMoneyContract && (
						<Button
							type="primary"
							htmlType="submit"
							onClick={(event) => {
								st.addNewContract(
									st.idStudent,
									st.idTeacher,
									st.nameStudent,
									st.nameTeacher,
									st.genderStudent,
									st.moneyTeacherPerHour,
									this.state.totalMoneyContract,
									this.state.selectedItems,
									this.state.checkedList,
									this.numberOfLesson,
									this.address,
									this.state.provinceName,
									this.state.districtName,
									this.state.wardName,
									this.hourPerLesson,
									this.note,
									this.state.dateContract,
									st.genderTeacher,
									st.imgTeacher,
									st.imgStudent,
									st.phoneTeacher,
									st.addressTeacher,
									st.addressStudent,
									st.skills,
									st.phoneStudent
								);

								this.err = 'Gi yu cu thnh cng';
							}}
						>
							Gi yu cu
						</Button>
					)}
				</Form.Item>
			</Form>
		);
	}
}

const HireOrder = Form.create({ name: 'form-order' })(HireOrderForm);
export default HireOrder;
